<template>
  <v-container>
    <v-card>
      <v-card-title>
    
        <app-autocomplete
        v-model="collection" 
        placeholder="select a collection"
        outlined
        dense
        :items="collectionsList"></app-autocomplete>
      </v-card-title>
      <v-card-text>
        <v-row>
          <v-col cols="12" v-for="(field, ind) in collectionFields" :key="ind" v-bind="field.col">
            
            <component 
                :is="inputs[field.input]"
                :field="field"
                outlined
                dense 
                v-bind="typeof field.bind == 'function' ? field.bind(vm) : field.bind"
                v-on="typeof field.on == 'function' ? field.on(vm) : field.on"
                v-model="data[field.name]"
              />
          

          </v-col>
        </v-row>
      </v-card-text>
    </v-card>
  </v-container>
</template>

<script>
import collections from './../collections'
import AppAutocomplete from './../components/Autocomplete' 

 import   {
  VTextField,
  VSelect,
  VSwitch,
  VTextarea
} from 'vuetify/lib'
 
import AppAutoComplete from './../components/Autocomplete'
import AppDatePicker from './../components/DatePicker'
import AppDateRange from '../components/DateRange'
import Assignment from '../components/Assignment'
import ProfileImage from '../components/ProfileImage'
import AppPassword from '../components/Password'
import AppMenuItemInput from '../components/MenuItemInput'
import Rights from '../components/Rights'

export default {
  components: {
    AppAutocomplete
  },
  data: () =>({
    pipeline: [],
    collection: null,
    data: {},
    types:[
      {
        name: 'Match'
      },
      {
        name: 'Group'
      },
      
    ]
  }),
  computed: {
    vm() { return this },
    inputs: () => ({
            text: VTextField,
            textarea: VTextarea,
            select: VSelect,
            switch: VSwitch,
            date: AppDatePicker,
            range: AppDateRange,
            autocomplete: AppAutoComplete,
            assignment: Assignment,
            'profile-image':ProfileImage,
            password: AppPassword,
            rights: Rights,
            'menu-items': AppMenuItemInput
    }),
    collections () {
      return collections.getAll()
    },
    collectionsList () {
      return Object.keys(this.collections).map(collection => ({
        text: this.collections[collection].title.plural,
        value: collection
      }))
    },
    collectionFields () {
      const c = this.collections[this.collection]
      if(!c) return []
      console.log(c);
      const panels = [...c.item.content.panels, ...c.item.sidebar.panels]
      let fields = []
      panels.forEach(panel => {
        if (panel.fields)
          fields = fields.concat(panel.fields)
      })
      return fields
    } 

  }

}
</script>

